.info-modal {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  z-index: 11;
  background-color: #dfdfdf;
  &__back {
    width: 82%;
    margin: 43px auto 10px;
    padding: 0 28px;
    svg {
      width: 30px;
      fill: #212121;
      cursor: pointer;
      &:hover,
      &:focus,
      &:active {
        fill: #000;
      }
    }
  }
  &__grid {
    display: grid;
    width: 82%;
    height: 82%;
    margin: 0 auto 42px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 2em;
    padding: 0 2em 1em;

    &-description {
      grid-column: 3/5;
      h3 {
        margin: 0;
      }
      div {
        width: 100%;
        height: 2px;
        background: #fd7e14;
        animation: underline 1s linear;
        transform-origin: left;
        margin: 0;
      }
      p {
        font-size: 17px;
      }
    }
    &-main {
      grid-column: 1/3;
      grid-row: 1/3;
    }
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  img:nth-child(4),
  :nth-child(3) {
    cursor: pointer;
  }
}

@keyframes underline {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}
