.ant-layout-footer {
  background-color: #000 !important;
  padding: 0 !important;
  height: 208px;
  display: flex;
}

.footer {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  &__info {
    color: #dddddd;
    img {
      width: 32px;
    }
    &-social {
      display: flex;
      align-items: flex-end;
      svg {
        width: 32px;
      }
    }
  }
}
