@media only screen and (max-width: 601px) {
  .menu-top {
    &__logo {
      display: none;
    }
    &__burger {
      width: 34px;
      margin-right: 2em;
      z-index: 150;
      position: fixed;
      margin-top: 67px;
      right: 0;
      transform: rotate(0deg);
      transition: 0.5s ease-in-out;
      span {
        display: block;
        position: absolute;
        height: 2.23px;
        width: 100%;
        background: #ff9800;
        margin-top: 8px;
        transform: rotate(0deg);
        transition: 0.25s ease-in-out;
      }
      span:nth-child(1) {
        top: 0;
        transform-origin: left center;
      }
      span:nth-child(2) {
        top: 0.6em;
        transform-origin: left center;
      }
      span:nth-child(3) {
        top: 1.2em;
        transform-origin: left center;
      }
      &.is-open {
        span:nth-child(1) {
          transform: rotate(45deg);
          top: 1.03em;
          left: 0.5em;
        }

        span:nth-child(2) {
          width: 0%;
          opacity: 0;
        }

        span:nth-child(3) {
          transform: rotate(-45deg);
          top: 2.65em;
          left: 0.5em;
        }
      }
    }
    .ant-menu {
      display: none !important;
    }
  }
}
