.hero {
  height: calc(100vh - 64px);
  background: #000;
  display: flex;
  align-items: center;
  img {
    width: 100%;
  }
  &__info {
    color: #dddddd;
    padding: 20%;

    h1 {
      color: #dddddd;
      font-weight: 600;
      font-size: 35px;
      line-height: 40px;
    }
    p {
      font-size: 16px;
      line-height: 18.4px;
    }
    div {
      text-align: center;
      svg {
        padding-top: 20px;
        text-align: center;
        width: 70px;
        height: 70px;
        fill: white;
      }
    }
  }
  .ant-row {
    justify-content: center;
  }
}
