@media only screen and (max-width: 601px) {
  .device-menu {
    position: fixed;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background: white;
    width: 100vw;
    height: 100vh;
    top: 0;
    right: 0;
    z-index: 1;
    ol {
      padding: 0;
      margin-top: 50px;
      text-align: center;
      color: #000;
      li {
        list-style: none;
        font-size: 22px;
        font-weight: 500;
      }
    }
    &__social {
      height: 100px;
      width: 100%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      svg {
        width: 32px;
      }
    }
  }
}
