@media only screen and (max-width: 601px) {
  .portfolio {
    h1 {
      font-size: 33px;
    }
    &__grid {
      display: block !important;
      div {
        margin: 30px auto;
        position: relative;
        width: 336px;
        height: 336px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    &__hover {
      position: absolute !important;
      top: 0;
      display: block;
      background: rgba(0, 0, 0, 0.8);
      margin: 0 !important;
      opacity: 0;
      border-radius: 20px;
      display: flex;
      color: #fff;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      p:first-of-type {
        margin: 0;
        border-bottom: 1px solid #fd7e14;
      }
      &:hover,
      &:focus {
        opacity: 1;
        transition: 1s;
      }
    }
  }
  .moddal {
    display: none;
  }
}
