.modal {
  width: 70%;
  height: 400px;
  margin: auto;

  &-body {
    height: 100% !important;
    padding: 0 !important;
  }
  &-dialog {
    max-width: 70% !important;
    height: 70% !important;
  }
  &-content {
    border-radius: 20px !important;
    width: 100% !important;
    height: 100% !important;
  }
}
.moddal {
  // display: none;
  position: fixed;
  z-index: 1;
  top: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.5);

  .swiper {
    &-pagination {
      bottom: 15px !important;
      &-bullet-active {
        background: #fd7e14 !important ;
      }
    }
    &-wrapper {
      height: 100%;
    }
    &-container {
      height: 100%;
      width: 100%;
      padding: 6em 15em;
      border-radius: 20px;
      top: 0;
    }
    &-button {
      &-next {
        left: 90%;
        color: #fd7e14 !important;
      }
      &-prev {
        left: 8%;
        top: 50%;
        color: #fd7e14 !important;
      }
    }
    &-slide {
      display: flex;
      width: 100% !important;
      // margin-right: 250px !important;
      div:nth-child(1) {
        width: 50%;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 20px 0 0 20px;
        }
      }
      div:nth-child(2) {
        width: 50%;
        padding: 40px;
        background: #dfdfdf;
        border-radius: 0 20px 20px 0;

        h2 {
          margin: 25px 0 0;
        }
        p {
          margin-top: 25px;
          font-size: 18px;
        }
        .underline {
          width: 100%;
          height: 1px;
          background: #fd7e14;
        }
        span:nth-child(1) {
          float: right;
          font-size: 28px;
          cursor: pointer;
          &:hover {
            color: #000;
          }
        }

        .btnMore {
          text-align: center;
          position: absolute;
          text-align: center;
          bottom: 6%;
          right: 2%;
          cursor: pointer;
          svg {
            fill: #212121;
            width: 32px;
            &:hover,
            &:focus,
            &:active {
              fill: #000;
            }
          }
          p {
            color: #212121;
            margin: 0;
            &:hover,
            &:focus,
            &:active {
              color: #000;
            }
          }
        }
      }
    }
    &-scrollbar {
      display: none;
    }
  }
}
