@media only screen and (max-width: 601px) {
  .modal-responsive {
    width: 100vw !important;
    height: 100vh !important;
    position: fixed !important;
    background: #eeeeee;
    top: 0 !important;
    margin: 0 !important;
    left: 0 !important;
    z-index: 150;
    overflow: scroll;
    &__container {
      width: 100% !important;
      height: 100% !important;
      &-svg {
        width: 90% !important;
        height: auto !important;
        svg {
          width: 25px;
        }
      }
      &-title {
        width: 90% !important;
        margin: auto !important;
        h3 {
          border-bottom: 2px solid #fd7e14;
          font-size: 18px;
          margin: 0;
        }
        h2 {
          font-size: 22px;
          margin-top: 37px;
          font-weight: 600;
        }
        p {
          font-size: 16px;
          color: #505050;
        }
        div {
          width: 100% !important;
          img {
            width: 100%;
            height: 150% !important;
            object-fit: cover;
            border-radius: 0 !important;
            margin-top: 20px;
          }
        }
      }
    }
  }
}
