.portfolio {
  &__hover {
    display: none;
  }
  &__underline {
    width: 80%;
    height: 2px;
    background: #505050;
    margin: auto;
    animation: underline 1s;
    transform-origin: left;
    &-invisible {
      display: none;
    }
  }

  h1 {
    width: 80%;
    font-size: 40px;

    margin: 35px auto 0;
  }
  &__arrow-visible {
    svg {
      fill: #505050;
      position: fixed;
      bottom: 30px;
      right: 10px;
      width: 32px;
    }
  }
  &__arrow-invisible {
    display: none;
  }
  &__grid {
    display: grid !important;
    width: 80%;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(6, 203px);
    margin: 32px auto;
    gap: 16px;
    div:nth-child(1) {
      grid-column: 1/3;
      grid-row: 1/3;
    }
    div:nth-child(4) {
      grid-column: 4/6;
      grid-row: 1/3;
    }
    div:nth-child(5) {
      grid-column: 1/4;
      grid-row: 3/5;
    }
    div:nth-child(6) {
      grid-column: 4/6;
      grid-row: 3/5;
    }
    div:nth-child(7) {
      grid-column: 1/3;
      grid-row: 5/7;
    }
    div:nth-child(8) {
      grid-column: 3/6;
      grid-row: 5/7;
    }

    img {
      cursor: pointer;
      width: 100%;
      object-fit: cover;
      height: 100%;
      border-radius: 20px;
    }
  }
}

@keyframes underline {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}
