$orange: #ff9800;
$grey: #505055;

.menu-top {
  display: flex;

  justify-content: space-between;
  &__logo {
    display: flex;
    width: 30px;
    margin-right: 40px;
    align-items: center;
    img {
      width: 30px;
      height: 30px;
    }
  }
  .ant-menu {
    background: #000 !important;
    &-item {
      padding: 0 10px;
      a {
        text-decoration: none !important;
        font-size: 14px !important;
        color: $orange !important;
      }
    }
  }

  .ant-menu-item-selected {
    background-color: transparent !important;
  }
}
