@media only screen and (max-width: 601px) {
  .hero {
    .ant-row {
      display: block !important;
      div {
        margin: auto;
        img {
          display: none;
        }
      }
      .ant-col-10 {
        max-width: 50% !important;
      }
    }
    &__info {
      padding: 0;
      h1 {
        font-size: 30px;
        line-height: 30px;
      }
      p {
        line-height: 22px;
      }
      svg {
        width: 43px;
      }
    }
  }
}

@media only screen and(max-width:480px) {
  .hero {
    .ant-row {
      .ant-col-10 {
        max-width: 56% !important;
      }
    }
  }
}
