@media only screen and (max-width: 601px) {
  .print {
    width: 80%;
    margin: 80px auto;
    span {
      color: #fd7e14;
    }
    h1 {
      margin: 0;
      font-size: 33px;
    }
    &__underline {
      height: 2px;
      background: #505050;
      margin: auto;
      animation: underline 1s;
      transform-origin: left;
      &-invisble {
        display: none;
      }
    }
    &__info {
      display: flex;
      width: 100%;
      height: 450px;
      overflow-x: scroll;
      margin-top: 15px;

      div {
        padding: 0 10px 0 0;
        img {
          width: 400px;
          height: 350px;
          object-fit: cover;
        }
        div {
          display: flex;
          margin-top: 10px;
          justify-content: space-between;
          align-items: center;
          padding: 0 17px;
          p {
            border-top: 1px solid var(--orange);
            font-size: 19px;
            margin: 0;
            font-weight: 600;
            color: #000;
          }
        }
      }
    }
  }
  @keyframes underline {
    0% {
      transform: scaleX(0);
    }
    100% {
      transform: scaleX(1);
    }
  }
}

@media only screen and (max-width: 480px) {
  .print {
    &__info {
      div {
        width: 300px;
        height: 400px !important;
        img {
          width: 100%;
          height: 300px;
        }
        div {
          height: auto !important;
          a {
            span {
              margin-right: 0 !important;
            }
          }
        }
      }
    }
  }
}
