.print {
  width: 80%;
  margin: 80px auto;
  span {
    color: #fd7e14;
  }
  h1 {
    margin: 0;
  }
  &__underline {
    height: 2px;
    background: #505050;
    margin: auto;
    animation: underline 1s;
    transform-origin: left;
    &-invisble {
      display: none;
    }
  }
  &__info {
    .swiper {
      &-container {
        width: 97% !important;
        margin: 20px 0px 0;
        z-index: 0 !important;
      }
      &-button-next {
        right: 0% !important;
        top: 40% !important;
        color: #fd7e14 !important;
      }
      &-button-prev {
        top: 40% !important;
        left: 97% !important;
        color: #fd7e14 !important;

        // display: none !important;
      }
      &-slide {
        width: 300px !important;
        height: 400px !important;

        div {
          width: 100%;
          img {
            width: 100%;
            height: 300px;
            object-fit: cover;
          }
          div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 13px;
            p {
              margin: 0;
              font-size: 21px;
              font-weight: 500;
              color: #000;
            }
          }
        }
      }

      &-button-prev.swiper-button-disabled {
        display: none !important;
      }
      &-button-next.swiper-button-disabled {
        display: none !important;
      }
    }
  }

